.container-Todoitem{
  display: flex;
  align-items: center;
  justify-content: center;
}

.TodoItem {
  width: 80%;
  background-color: #ebdbf6;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  border: 2px solid #676e6f;
  border-radius: 20px;
  outline: none;
}


.TodoItem-p {
 
  margin: 24px 0 24px 24px;
  width: calc(90% - 100px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
 
  
}
.TodoItem-p
.completed {
  
  text-decoration: line-through;
 
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;
  font-size: 24px;
  font-weight: bold;
  background-color: transparent; 
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4d954f;
}

.Icon-delete {
  position: absolute;
  top: -20px;
  right: 0;
  width: 48px;
  height: 48px;
}
.Icon-delete:hover {
  color: red;
}